@import url(http://fonts.googleapis.com/earlyaccess/notosanskr.css);
@import url(http://fonts.googleapis.com/earlyaccess/notosanskr.css);
body {
    font-family: 'Noto Sans KR', sans-serif;
    background-color: #f7f7f7;
    padding: 0;
    margin: 0;
}

.logo {
    font-size: 30px;
    font-weight: 800;
    color: #7e57d4;
    background-color: white;
    height: 100px;
    margin-top: 30px;

    display: flex;
    justify-content: center;
    align-items: center;
}

.logo-text {
    cursor: pointer;
}

.container {
    padding: 0 150px;
}

.card {
    margin-top: 30px;
    margin-bottom: 40px;
    border-radius: 10px;
    box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.05);
    background-color: #7e57d4;
    padding: 15px 20px 20px 20px;
}

.card-title-input {
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 10px;

    width: 100%;
    font-family: 'Noto Sans KR', sans-serif;
    border: none;
    outline: none;
    background-color: #7e57d4;
    color: white;
    height: 30px;
}

.card-title-input::-webkit-input-placeholder, .card-desc-input::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
}

.card-title-input:-ms-input-placeholder, .card-desc-input:-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
}

.card-title-input::-ms-input-placeholder, .card-desc-input::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
}

.card-title-input::placeholder, .card-desc-input::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
}

.card-title-input:-ms-input-placeholder, .card-desc-input:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: white;
}

.card-title-input::-ms-input-placeholder, .card-desc-input::-ms-input-placeholder { /* Microsoft Edge */
    color: white;
}

.card-desc-input {
    width: 100%;
    font-family: 'Noto Sans KR', sans-serif;
    border: none;
    outline: none;
    background-color: #7e57d4;
    color: white;
    height: 20px;
}

.icon-div {
    text-align: center;
    margin: 20px 0 0 0;
}

.plus-icon {
    width: 50px;
    cursor: pointer;
    padding-bottom: 30px;
}

.plus-icon:hover {
    opacity: 0.7;
}

.question-card {
    border-radius: 7px;
    border: solid 1px #cccccc;
    box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.05);
    background-color: white;
    width: 100%;
    padding: 20px;
    margin-bottom: 20px;
}

.select-div {
    float: right;
    position: relative;
    width: 150px;
}

select {
    line-height: 1.3;
}

.select {
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 13px;
    width: 100%;
    height: 30px;
    border: solid 1px #d6d6d6;
    -webkit-appearance: none;
    padding: 5px 10px;
    /* line-height: 1; */

    background-color: #ffffff;
    background-image: url(/ringle-survey/static/media/dropdown.76bb9659.svg); /* For Chrome and Safari */
    background-position: center right;
    background-repeat: no-repeat;
}

.header {
   font-size: 17px;
   font-weight: 600;
   margin-bottom: 10px;

    width: 80%;
    font-family: 'Noto Sans KR', sans-serif;
    border: none;
    outline: none;
    color: black;
}

.header::-webkit-input-placeholder {
    color: black;
    opacity: 1;
}

.header:-ms-input-placeholder {
    color: black;
    opacity: 1;
}

.header::-ms-input-placeholder {
    color: black;
    opacity: 1;
}

.header::placeholder {
    color: black;
    opacity: 1;
}

.header:-ms-input-placeholder { 
    color: black;
}

.header::-ms-input-placeholder { 
    color: black;
}

.meta {
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 20px;

    width: 80%;
    font-family: 'Noto Sans KR', sans-serif;
    border: none;
    outline: none;
    color: #a6a6a6;
}

.meta::-webkit-input-placeholder {
    color: #a6a6a6;
    opacity: 1;
}

.meta:-ms-input-placeholder {
    color: #a6a6a6;
    opacity: 1;
}

.meta::-ms-input-placeholder {
    color: #a6a6a6;
    opacity: 1;
}

.meta::placeholder {
    color: #a6a6a6;
    opacity: 1;
}

.meta:-ms-input-placeholder { 
    color: #a6a6a6;
}

.meta::-ms-input-placeholder { 
    color: #a6a6a6;
}

.input {
    height: 30px;
    margin-left: 10px;
    border: solid 1px #d6d6d6;
    background-color: #ffffff;
    padding: 7px 10px;
}

.bottom-gap {
    margin-bottom: 10px;
}

.remove-text {
    margin-left: 10px;
    font-size: 11.5px;
    font-weight: 500;
    color: red;
    cursor: pointer;
}

.remove-text:hover {
    text-decoration: underline;
}

.add-text {
    margin-left: 10px;
    font-size: 11.5px;
    font-weight: 500;
    color: blue;
    cursor: pointer;
}

.add-text:hover {
    text-decoration: underline;
}

.textarea {
    width: 100%;
    padding: 10px;
    border: solid 1px #d6d6d6;
}

.delete-btn {
    text-align: end;
    margin-top: 10px;
}

.negative {
    font-family: 'Noto Sans KR', sans-serif !important;
    font-size: 12px !important;
    font-weight: 500 !important;
}


.submit-div {
    position: fixed;
    right: 0;
    bottom: 0;
    padding-bottom: 30px;
    
}

.submit-btn {
    font-family: 'Noto Sans KR', sans-serif !important;
    background-color: #7e57d4 !important;
    color: white !important;
    font-size: 14px !important;
    font-weight: 500 !important;
}

.submit-btn:hover {
    background-color: #734fc2 !important;
}
